<script>
import Vue from 'vue'
import {ClientTable, Event} from 'vue-tables-2'
import TrainingProgress from './TrainingProgress'
import {restapiPrefix} from './variables.js'
import miniToastr from 'mini-toastr';

const toastTypes = {
  success: 'success',
  error: 'error',
  info: 'info',
  warn: 'warn'
}

const options = {
  success: toast,
  error: toast,
  info: toast,
  warn: toast
}

miniToastr.init({types: toastTypes})

function toast ({title, message, type, timeout, cb}) {
  return miniToastr[type](message, title, timeout, cb)
}

Vue.use(ClientTable)

export default {
  name: 'DashboardTable',
  props: ["columns", "data", "options","theme", "userId"],
  components: {
    ClientTable,
    Event,
    TrainingProgress,
  },
  data () {
    return {
      deleteCaregiverModal: false,
      deactivateCaregiverModal: false,
      selectedCaregiverName: "",
      selectedCaregiverId: "",
      selectedCaregiverUserId: "",
      tabIndex: 0,
      // selectedUserId: "",
    }
  },
  methods: {
    rotateIcon: function (event) {
      // console.log(event)
      // var clickedElement = event.target;
      // $(clickedElement).toggleClass('down');
      event.toggleClass("down");
    },
    showDeleteCaregiverModal: function (caregiver) {
      this.deleteCaregiverModal = true;
      this.selectedCaregiverName = caregiver.first_name + " " + caregiver.last_name;
      this.selectedCaregiverId = caregiver.id;
      this.selectedCaregiverUserId = caregiver.user_id;
      // this.selectedUserId = caregiver.user_id;
    },
    deleteCaregiver: function() {
      let asksUri = restapiPrefix+'/caregivers/'+this.selectedCaregiverId+'/'+this.selectedCaregiverUserId;
      this.axios.delete(asksUri).then(response => {
        this.$emit('getCaregiverListByUserId', this.userId);
        miniToastr.success('Caregiver deleted successfully!', 'Success');
        location.reload();
      });
    },
    showDeactivateCaregiverModal: function (caregiver) {
      this.deactivateCaregiverModal = true;
      this.selectedCaregiverName = caregiver.first_name + " " + caregiver.last_name;
      this.selectedCaregiverId = caregiver.id;
      // this.selectedUserId = caregiver.user_id;
    },
    deactivateCaregiver: function() {
      let asksUri = restapiPrefix+'/caregivers/activationCaregiverByID/'+this.selectedCaregiverId;
      this.axios.put(asksUri, {
          is_activated: 0,
        }) 
        .then(response => {
          // this.getGroupListByUserId(1);
          // console.log(response);
          miniToastr.success('Deactivated Caregiver!', 'Successfully');
          this.$emit('getCaregiverListByUserId', this.userId);
        })
        .catch(function (error) {
          miniToastr.error('something went wrong!', 'Error');
          console.log(error);
        });

      /*this.axios.put(asksUri).then(response => {
        console.log(response.data);
      });*/
    },
    isMobile() {
      if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
        console.log("mobile")
        return true
      } else {
        console.log("desktop")
        return false
      }
    }
  }
}
</script>

