            <b-tab active>
              <template slot="title">
                <i class="icon-people"></i> {{tabs[0]['title']}}
              </template>
              <b-card class="d-none d-lg-block new-caregiver-btn">
                <b-button block pill variant="primary" size="sm" @click="user_access==true?packageLimitModal = true:newCaregiverModal = true" class="form-control" :disabled='isDisabled'><i class="fa fa-plus fa-lg"></i>&nbsp;&nbsp;NEW CAREGIVER</b-button>
              </b-card>
              <DashboardTable :columns="columns" :data="caregivers" :options="options" :theme="theme" :userId="userId" v-on:getCaregiverListByUserId="getCaregiverListByUserId"></DashboardTable>

            </b-tab>
            <b-tab>
              <template slot="title">
                <i class="fa fa-list"></i> {{tabs[1]['title']}}
              </template>
              <b-card class="d-none d-lg-block new-caregiver-btn">
                <b-button block pill variant="primary" size="sm" @click="user_access==true?packageLimitModal = true:newCaregiverModal = true" class="form-control" :disabled='isDisabled'><i class="fa fa-plus fa-lg"></i>&nbsp;&nbsp;NEW CAREGIVER</b-button>
              </b-card>
                <DashboardTable :columns="columnsInitial" :data="caregivers" :options="options" :theme="theme" :userId="userId" v-on:getCaregiverListByUserId="getCaregiverListByUserId"></DashboardTable>
          
            </b-tab>
            <b-tab>
              <template slot="title">
                <i class="icon-calendar"></i> {{tabs[2]['title']}}
              </template>
              <b-card class="d-none d-lg-block new-caregiver-btn">
                <b-button block pill variant="primary" size="sm" @click="user_access==true?packageLimitModal = true:newCaregiverModal = true" class="form-control" :disabled='isDisabled'><i class="fa fa-plus fa-lg"></i>&nbsp;&nbsp;NEW CAREGIVER</b-button>
              </b-card>
                <DashboardTable :columns="columnsAnnualSoon" :data="caregivers" :options="options" :theme="theme" :userId="userId" v-on:getCaregiverListByUserId="getCaregiverListByUserId"></DashboardTable>
          
            </b-tab>
            <b-tab>
              <template slot="title">
                <i class="icon-clock"></i> {{tabs[3]['title']}}
              </template>
              <b-card class="d-none d-lg-block new-caregiver-btn">
                <b-button block pill variant="primary" size="sm" @click="user_access==true?packageLimitModal = true:newCaregiverModal = true" class="form-control" :disabled='isDisabled'><i class="fa fa-plus fa-lg"></i>&nbsp;&nbsp;NEW CAREGIVER</b-button>
              </b-card>
                <DashboardTable :columns="columnsAnnualOverDue" :data="caregivers" :options="options" :theme="theme" :userId="userId" v-on:getCaregiverListByUserId="getCaregiverListByUserId"></DashboardTable>
          
            </b-tab>
                <!-- Add your b-tab components here -->
            <template v-slot:tabs-end>
              <li class="nav-item align-self-center">
                <!-- Actual search box -->
                <div class="form-group has-search">
                  <span class="fa fa-search form-control-feedback"></span>
                  <input type="text" class="form-control" placeholder="" >
                </div>
                
              </li>
            </template>
          </b-tabs>
        </b-card>
      </b-col>
      <!--<b-col xs="3" lg="3">
        <b-card class="d-none d-lg-block">
            <b-button block pill variant="primary" size="sm" @click="newCaregiverModal = true" class="form-control" :disabled="user_access"><i class="fa fa-plus fa-lg"></i>&nbsp;&nbsp;NEW CAREGIVER</b-button>
          
          <b-button block pill size="sm" @click="newCaregiverModal = true" class="form-control "><i class="fa fa-print"></i>&nbsp;&nbsp;PRINT REPORTS</b-button>

        </b-card>

        <b-card> -->
          <!-- Actual search box -->
         <!-- <div class="form-group has-search">
            <span class="fa fa-search form-control-feedback"></span>
            <input type="text" class="form-control" placeholder="Search">
          </div>

          <b-list-group class="user-list-group">
            <b-list-group-item v-for="user in user_arr" :key="user['name']" class="d-flex justify-content-between align-items-center">
              <b-img fluid class="img-avatar" src="img/avatars/6.jpg"></b-img>
              {{user['name']}}
              <b-badge variant="primary" pill>{{user['latest_date']}}</b-badge>
            </b-list-group-item>
          </b-list-group>
        </b-card>
      </b-col>-->
    </b-row>
    <b-modal title="Create Caregiver" class="modal-primary modal-header" v-model="newCaregiverModal" @ok="newCaregiverModal = false">
      <template v-slot:modal-header>
        <b-button size="lg" variant="primary" class="btn-4x modal-header-icon"><i class="cui-user"></i></b-button>&nbsp;&nbsp;<h4>Create Caregiver</h4>  
      </template>
      <div md="12" sm="12">
        <CaregiverRegister :userId="userId" v-on:closeNewCaregiverModalDialog="closeNewCaregiverModal"></CaregiverRegister>
      </div>
      <div slot="modal-footer"></div>
    </b-modal>

    
    <b-modal title="Reached package limit" class="modal-primary modal-header" v-model="packageLimitModal" @ok="packageLimitModal = false">
      <template v-slot:modal-header>
        <b-button size="lg" variant="primary" class="btn-4x modal-header-icon"><i class="cui-bell"></i></b-button>&nbsp;&nbsp;<h4>You have reached limit for caregiver, to add caregiver please update package.</h4>  
      </template>
      <div md="6" sm="6">
        <b-button size="sm" variant="primary" class="btn-4x modal-header-icon update-btn btn_manage" @click="redirect_to_pricing">Update Packages</b-button>
      </div>
      <div md="6" sm="6">
        <b-button size="sm" variant="secondary" class="btn-4x modal-header-icon update-btn" @click="clspackageLimitModal">Cancel</b-button>
      </div>
      <div slot="modal-footer"></div>
    </b-modal>

    <div class="vld-parent">
        <loading :active.sync="isLoading" 
        :can-cancel="true" 
        :is-full-page="fullPage"
        color="#06b">
        </loading>
    </div>
  </div>
  
</template>

<script>
import Vue from 'vue'
import axios from 'axios';
import DashboardTable from './DashboardTable.vue'
import CaregiverRegister from './pages/CaregiverRegister'
import {restapiPrefix,wprestapiPrefix} from './variables.js'
// Get temp progress info using random functions
import { random } from '@/shared/utils'
import router from '../router';
import { ClientTable } from "vue-tables-2";
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import miniToastr from 'mini-toastr';

const toastTypes = {
  success: 'success',
  error: 'error',
  info: 'info',
  warn: 'warn'
}

const options = {
  success: toast,
  error: toast,
  info: toast,
  warn: toast
}

miniToastr.init({types: toastTypes})

function toast ({title, message, type, timeout, cb}) {
  return miniToastr[type](message, title, timeout, cb)
}

//let options = {};
let useVuex = false;
let theme = "bootstrap4";
let template = "default";
Vue.use(ClientTable, options, useVuex, theme, template);

export default {
  name: 'dashboard',
  components: {
    DashboardTable,
    CaregiverRegister,
    Loading
  },
  data: function (){
    return {
      tab_selector: '',
      tabIndex: 0,
      newCaregiverModal: false,
      packageLimitModal : false,
      userId: "1",
      search: '',
      user_access:true,
      isDisabled_v:false,
      currentLevelId:0,
      progress:[],
      userListWithCourses:[],
      isLoading: false,
      fullPage: true,
      tabs: [
        {
          title: 'ALL CAREGIVERS',
          icon: 'fa fa-plus'
        },
        {
          title: 'INITIAL TRAINING',
          icon: 'fa fa-list'
        },
        {
          title: 'ANNUAL DUE DATE SOON',
          icon: 'fa fa-calendar'
        },
        {
          title: 'ANNUAL DUE DATE OVERDUE',
          icon: 'fa fa-clock'  
        }
      ],
      user_arr: [
        {
          name: 'David James',
          latest_date: 'Jan 5',
        },
        {
          name: 'Tony Stark',
          latest_date: 'Jan 4',
        },
        {
          name: 'Bessie Berry',
          latest_date: 'Mar 25',
        },
        {
          name: 'Gleb Kuznestsov',
          latest_date: 'Apr 14',
        },
        {
          name: 'Andrey Prokopenko',
          latest_date: 'Dec 30',
        },
      ],
      columns: ['name', 'initial', 'progress', 'due_date', 'message_date'],
      columnsInitial: ['name', 'initial', 'due_date', 'message_date'],
      columnsAnnualSoon: ['name', 'progress', 'due_date', 'message_date'],
      columnsAnnualOverDue: ['name', 'progress', 'due_date', 'message_date'],
      caregiverGroups: [],
      caregiverGroup: [
        {
          tabName: 'ABC',
          id: 1,
        },
        {
          tabName: 'EGS',
          id: 2,
        },
        {
          tabName: 'GEG',
          id: 3,
        },
        {
          tabName: 'Non-medical Caregivers',
          id: 4,
          isDefault: true,
        },
        {
          tabName: 'TYH',
          id: 5,
        },
        {
          tabName: 'IEH',
          id: 6,
        },
      ],
      caregivers: [],
      options: {
        headings: {
          name: 'Name',
          phone_number: 'Phone',
          initial: 'Initial training progress',
          progress: 'Progress',
          due_date: 'due date',
          message_date: 'Messaging',
        },
        columnsClasses: {
          initial: 'initial-class',
          progress: 'progress-class',
        },
        sortable: ['name', 'initial', 'progress', 'due_date', 'message'],
        filterByColumn: true,
        filterable: ['name'],
        filterAlgorithm: {
          name(row, query) {
            return (row.first_name + ' ' + row.last_name).includes(query);
          }
        },
        sortIcon: { base:'fa', up:'fa-sort-asc', down:'fa-sort-desc', is:'fa-sort' },
        pagination: {
          chunk: 5,
          edge: false,
          nav: 'scroll'
        }
      },
      theme: 'bootstrap4',
    }
  },
  beforeCreate: function () {  
    if(localStorage.user_id == undefined || localStorage.user_id == "0")
    {        
      router.push({ name: "Login"})
    }   
  },
  created() {
    this.userId = localStorage.user_id;
    this.isLoading = true;
    this.getProgress();
    this.checkUserAccess();
  },
  methods: {
    tabSelect(e) {
      this.tabIndex = this.tabs.indexOf(e)
    },
    closeNewCaregiverModal() {
      this.newCaregiverModal = false;
      this.checkAssignedCourses();
      this.checkUserAccess();   
      this.getProgress();
      //this.getCaregiverListByUserId(this.userId);
      
      //location.reload();
    },
    getCaregiverListByUserId(userId) {
      // Get Caregiver groups by logged in user's Id
      this.caregivers= [];
      let asksUri = restapiPrefix+'/caregivers/getCaregiversByUserID/'+userId;
      this.axios.get(asksUri).then(response => {
        if(response.data.length > 0)
        {
          for(let i = 0; i < response.data.length; i++) {
            let caregiver = response.data[i];
            //caregiver["initial"] = random(10, 100);
            caregiver["progress"] = this.getProgressByCaregiverId(response.data[i]['user_id'],'annual');
            caregiver['initial'] = this.getProgressByCaregiverId(response.data[i]['user_id'],'initial');
            caregiver['courses'] = this.getProgressByCaregiverId(response.data[i]['user_id'],'courses');
            caregiver["group_name"] = this.getGroupNamebyGroupId(response.data[i]["caregiver_group"]);
            this.caregivers.push(caregiver);
            this.isLoading = false;
            console.log('this.caregivers==>'+JSON.stringify(this.caregivers));
          }
        }
        else{
          miniToastr.info('There are no caregivers added yet!', 'Info');
          this.isLoading = false;
        }
      });
    },
    getProgress(){
      var data = {
            'user_id':this.userId
      }
        axios.post(wprestapiPrefix+'/wp-json/custom-plugin/fn_get_user_with_courses_MVEN',data).then(response => {
            console.log('response==>'+response.data['status']);
            if(response.data['status'] == 'success')
            {
              this.userListWithCourses = response.data['data'];
              console.log('this.userListWithCourses==>'+this.userListWithCourses);
            }
            else{
              console.log('something went wrong!');
              miniToastr.error('something went wrong!', 'Error');
            }
        });

        axios.post(wprestapiPrefix+'/wp-json/custom-plugin/fn_get_progress_MVEN',data).then(response => {
          console.log('response==>'+response.data['status']);
          if(response.data['status'] == 'success')
          {
            this.progress = response.data['data'];
            this.getCaregiverListByUserId(this.userId);
            console.log('this.progress==>'+this.progress);
          }
          else{
            console.log('something went wrong!');
            miniToastr.error('something went wrong!', 'Error');
          }
      });
    },
    redirect_to_pricing()
    {
      let level = 1;
      let Ccount = parseInt(this.currentLevelId);
      if(Ccount >= 5 && Ccount < 10)
      {
        level = 2
      }
      else if(Ccount >= 10 && Ccount < 25){
        level = 3
      }
      else if(Ccount >=25)
      {
        level = 4
      }
      window.open(
        wprestapiPrefix+'/membership-account/membership-checkout/?level='+level+'&uid='+btoa(this.userId),
        '_blank' // <- This is what makes it open in a new window.
      );
      this.packageLimitModal = false;
      this.getCaregiverListByUserId(this.userId);
      this.checkUserAccess();
    },
    clspackageLimitModal(){
      this.packageLimitModal = false;
      this.getCaregiverListByUserId(this.userId);
      this.checkUserAccess();
    },
    getProgressByCaregiverId(user_id,category){
      let p = 0;
      if(category == 'initial')
      {
        for(var i = 0; i < this.progress.length; i++) {
          if(this.progress[i].user_id == user_id) {
            p=this.progress[i].initial.toFixed(2);
            return p;
          }
        }
        return p;
      }
      else if(category == 'annual'){
        for(var j = 0; j < this.progress.length; j++) {
          if(this.progress[j].user_id == user_id) {
            p=this.progress[j].annual.toFixed(2);
            return p;
          }
        }
        return p;
      }
      else if(category == 'courses')
      {
        for(var k = 0; k < this.userListWithCourses.length; k++) {
          if(this.userListWithCourses[k].user_id == user_id) {
            p=this.userListWithCourses[k];
            return p;
          }
        }
        return p;
      }
    },
    checkUserAccess()
    {
      var data = {
            'user_id':this.userId
      }

        axios.post(wprestapiPrefix+'/wp-json/custom-plugin/fn_check_access_MVEN',data).then(response => {

          console.log('response==>'+response.data['status']);
          if(response.data['status'] == 'success')
          {
            this.user_access = response.data['access'];

            if(this.user_access == true){
              var reason = response.data['reason'];
              if(reason == 'verification')
              {
                this.isDisabled_v = true;
                miniToastr.info('Please verify your email address to add caregiver', 'Info');
              }
              else{
                this.isDisabled_v = false;
              }
            }
            
            let asksUri_i = restapiPrefix+'/caregivers/getCaregiverCount/'+this.userId;
            this.axios.get(asksUri_i).then(res => {
              
              var len = 0;
              //this.currentLevelId = response.data['membership_level'].id;
              if(res.data != '')
              {
                len = res.data;
              }
              else{
                len = 0 ;
              }
              this.currentLevelId = len;
              if(response.data['membership_level'] != undefined && len >= parseInt(response.data['membership_level'].description))
              {
                  this.user_access = true;
                  if(localStorage.UpdatePopup !== 'false')
                  {
                    localStorage.UpdatePopup = false;
                    this.packageLimitModal = true;
                  }
              }
            });  
          }
          else{
              console.log(response.data['message']);
              miniToastr.error(response.data['message'], 'Error');
          }
      });
    },
    getGroupNamebyGroupId (group_id) {
      for(let i = 0; i < this.caregiverGroups.length; i++) {
        if (this.caregiverGroups[i]["id"] == group_id) {
          return this.caregiverGroups[i]["group_name"]
        }
      }
      return "";
    },
    getAllCaregiverListByUserId(userId) {
      // Get Caregiver groups by logged in user's Id
      let asksUri = restapiPrefix+'/caregiverGroups/getGroupsByUserID/'+userId;
      this.axios.get(asksUri).then(response => {
        this.caregiverGroups = response.data;
        this.getCaregiverListByUserId(userId);
      });
    },
    checkAssignedCourses(){
      var classList = [];
      let asksUri_2 = restapiPrefix+'/caregiverCurriculum/getAllCurriculumsByUserIdAndInitial/'+this.userId+'/1';//this.is_initial;
      this.axios.get(asksUri_2).then(response => {
        if(response && response.data.length>0)
        {
          for(let i = 0; i < response.data.length; i++) {
            classList[i]=response.data[i]['class_id'];
          }
          console.log('classList==>'+classList);
          this.assignCourses(classList);
        }
        else{
          let asksUri_3 = restapiPrefix+'/caregiversClass/getDefaultCourses';
          this.axios.get(asksUri_3).then(response => {
            for(let i = 0; i < response.data.length; i++) {
              classList[i]=response.data[i]['class_id'];
            }
            console.log('classList==>'+classList);
            this.assignCourses(classList);
          });
        }
      });
    },
    assignCourses(classList)
    {
       var date2 = new Date().toJSON().slice(0,10).replace(/-/g,'/')
        let asksUri_1 = restapiPrefix+'/caregiverCurriculum/updateByUserIdAndInitial/'+this.userId+'/1';//+localStorage.is_initial;
        axios.put(asksUri_1, {
          classIdList: classList,
          assigned_date: date2,
          assigned_by: this.assignedBy
        })
        .then(response => {

          var data = {
            'user_id':this.userId,
            'classIdList':classList
          }
          
          //axios.post('http://localhost:8100/onlinecourses_wp/wp-json/custom-plugin/fn_assign_courses_MVEN',data).then(response => {
          axios.post(wprestapiPrefix+'/wp-json/custom-plugin/fn_assign_courses_MVEN',data).then(response => {
            console.log('response==>'+response.data['status']);
            if(response.data['status'] == 'success')
            {
            }
          });

        })
        .catch(function (error) {
          console.log(error);
        });
    },
  },
  computed:{
    isDisabled: function(){
    	return this.isDisabled_v;
    }
  }
}
</script>


